
footer{  
    height: 50vh !important;
    background-color: none;
    max-height: 100px;
  }

  //footer
footer{
  text-align: center;
  color: white;   
  position: relative;
  display: block;
  .container{
    padding: 50px 0 30px 0;
    height: auto;
    @media (min-width: 768px){
      max-height: 259px;
    }
    
  }
    #hagamos{
      border-right: 1px solid white;
      
      @media (max-width: 575px){
        border: none;
        padding-left: 32px;

      }
    }
    .contactanos{      
      font-size: 35px; 
      font-weight: 300;  
      @media (max-width: 575px){
        border-bottom: 1px solid white;
        margin-bottom: 20px;
        padding-bottom: 10px;
      }   
    }
    .project{
      margin-top: 20px;
      font-size: 16px;      
      font-weight: 100;     
    }
    .contact{
      a{
        font-weight: 200;
        text-decoration: none;
        font-size: 16px;   
        border-bottom: 1px solid;   
        line-height: 2em;
        transition-duration: 300ms;
        color: white;
        
        &:hover{
          color: gray;
        }
      }
      
    }    
    .mail{
      font-size: 18px;      
      margin-bottom: 10px;
      border-bottom: 1px solid;
    }
    .siguenos{
      font-weight: 200;
      margin: 20px 0 0 0;
    }
    .social-icons{      
      margin-top: 1rem;
      .icon{    
        border-radius: 50%;
        border: solid;
        border-width: 1px;
        border-color: white;
        color: white;
        transition-duration: 500ms;
        &:hover{
          background-color: $magenta;
          border: none;
        }
        width: 3rem;
        height: 3rem;
        position: relative;
        display: inline-block;    
        margin-left: 1rem;  
        margin-right: 1rem;  
        .icon-text{
          
          font-weight: 300;
          font-size: 12px;
          margin: 0;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
    }
    #contacto-animacion{
      margin-top: 0 !important;
      @media (max-width: 575px){
        padding-left: 34px;
        position: relative;
        margin-top: 30px !important;
      }
      
    }
    .copyright{      
      font-size: 11px;  
      padding: 10px 0 30px 0; 
      bottom: 0;
    }
}

.glow{
  @media (min-width: 768px) {
    background: 50% 100% / 50% 50% no-repeat
      radial-gradient(ellipse at bottom, #fff, transparent, transparent);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    font-family: "Source Sans Pro", sans-serif;
    animation: reveal 3000ms ease-in-out forwards 200ms,
              glow 2500ms linear 2000ms;

    @keyframes reveal {
      80%{
        letter-spacing: 4px;
      }
      100% {
        background-size: 300% 300%;
      }
    }
    @keyframes glow {
      40% {
        text-shadow: 0 0 8px #fff;
      }
    }
  }
  
}

.glowInf{
  
    background: 50% 100% / 50% 50% no-repeat
      radial-gradient(ellipse at bottom, #fff, transparent, transparent);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent !important;
    font-family: "Source Sans Pro", sans-serif;
    animation: reveal 3000ms ease-in-out forwards 200ms,
              glow 2500ms linear infinite 2000ms;

    @keyframes reveal {
      80%{
        letter-spacing: 4px;
      }
      100% {
        background-size: 300% 300%;
      }
    }
    @keyframes glow {
      40% {
        text-shadow: 0 0 8px #fff;
      }
    }
  
}



